import {
	getGoalc,
	getYdList,
	getJkList,
} from '../../service/api/motion.js' //运动api
import {
	getisLogin,
} from '../../service/api/login.js' //登陆api
//通知数据
const notice={
	contentmain:"请连接蓝牙进行操作",
	content:{
		0:'请连接蓝牙设备和打开位置服务进行操作',
		1:'坚持运动'
	}
}

export default {
	data() {
		return {
			//通知
			notice: notice,
			//计划完成度
			goalC: '',
			//运动数据
			ydList: '',
			//健康数据
			jkList: '',
			//健康数据切换
			TabCur:'sleep'
		}
	},
	mounted(){
		getisLogin() //是否登陆
		this.getData()//获取数据
	},
	methods: {
		getData(){
			var _this=this
			//获取数据计划完成度
			getGoalc()
			.then(res => {
				//console.log(res)
				_this.goalC=res.data
			}).catch(err => {
				
			})
			
			//获取运动数据
			getYdList()
			.then(res => {
				//console.log(res)
				_this.ydList=res.data
			}).catch(err => {
				
			})
			
			//获取健康数据
			getJkList()
			.then(res => {
				//console.log(res)
				_this.jkList=res.data
			}).catch(err => {
				
			})
		},
		noticeClose(e){
			//关闭通知
			this.notice.contentmain=""
		},
		noticeRun(index){
			/**
			 * 通知轮询
			 * @param {index} 通知内容下标  
			 */
		},
		tabSelect(e){
			//健康切换
			this.TabCur = e.currentTarget.dataset.id
		},
		ydToUrl(e){
			//运动
			const item=this.ydList[e]
			
			const datas= JSON.stringify({
				'title':item.title,
				'bgcolor':item.bgcolor,
				'title_total_0':item.title_total_0,
				'content_total_0':item.content_total_0,
				'title_total_1':item.title_total_1,
				'content_total_1':item.content_total_1,
				'title_total_2':item.title_total_2,
				'content_total_2':item.content_total_2,
			})
			console.log(datas)
			if(datas.indexOf("title_total_0")!=-1){
				uni.navigateTo({
					url: './motion/children/motion_ydData?id=1&datas='+datas,
					animationType: 'pop-in',
					animationDuration: 200
				});
			}
			
		}
		
	}
}
